#control-panel h2, h1 {
  color: #28265D;
}

#control-panel  h1 {
  text-align: center;
}


#control-panel button {
  border: 1px solid;
  background-color: #28265D;
  border-radius: 5px;
  width: 100px;
  height: 50px;
  font-size: 90%;
  color: #FBF6EB;
  font-family: inherit;
}

#control-panel button:hover {
  background-color: #44419E;
}

#control-panel .commit {
  color: #F51600;
  font-weight: bold;
  background-color: inherit;
  margin: 25px;
}
#control-panel .commit:hover {
  background-color: #E3DECA;
}
.form {
  border: 3px solid  #28265D;
  margin: 30px auto;
  padding: 30px;
  border-radius: 10px;
}
